@import url(https://fonts.googleapis.com/css?family=Anonymous+Pro&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.main {
    padding-top: 55px;
    background-color: #F4F4F4;
}
a {
    color: inherit;
    text-decoration: none;
}

.my-icons {
    margin: 10px;
}

.first-icon {
    margin-left: 0;
}

.project-button {
    margin-right: 10px;
}

h1 {
    text-align: center;
    color: #005691;
}

.my-deck {
    margin: 5px 10px 5px 10px;
    padding: 30px;
    -webkit-animation: fadein 2s; /* Safari, Chrome and Opera > 12.1 */ /* Firefox < 16 */ /* Opera < 12.1 */
    animation: fadein 2s;
}
@keyframes fadein {
    from {
        opacity:0;
    }
    to {
        opacity:1;
    }
}
@-webkit-keyframes fadein { /* Safari and Chrome */
    from {
        opacity:0;
    }
    to {
        opacity:1;
    }
}

@media (max-width: 768px) {
    #Home {
        background: url(/static/media/image.5eb866a7.jpg) no-repeat;
        /* background-color: black; */
        background-size: contain;
        height: 100vh;
        background-blend-mode: darken

    }
}
@media (min-width: 769px) {
    #Home {
        /* background: #url("https://therightsofnature.org/wp-content/uploads/2018/01/turkey-3048299_1920.jpg") no-repeat; */
        /* background: url("https://therightsofnature.org/wp-content/uploads/2018/01/turkey-3048299_1920.jpg"); */
        background-image: url(/static/media/img2.175d73e2.png);
        background-size: cover;
        color: white;
        height: 100vh;
        background-blend-mode: darken;
    }
}

@media (min-width: 769px) {
    #Skills {
        height: 100vh;
    }
}

@media (max-width: 768px) {
    #Skills {
        height: auto;
    }
}

#About {
    height: 100vh;
}

#About {
    margin: 0 auto;
    width: 100%;
    background-color: #fefefe;
}

#Skills {
    background-color: #FEFEFE;
}

#Projects {
    background-color: #F2F2F2;
}
.name-intro {
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.name-intro-header {
    margin: 0;
    position: absolute;
    top: 40%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.my-about {
    position: relative;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}
.box {
    border: #005691 1px solid;
}
